@import "./variables";

.global-layout {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto;
  position: relative;
  .layout-body {
    width: 95%;
    max-width: 1400px;
    margin: 0 auto;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  align-items: center;

  .nav-logo {
    height: 70px;
  }

  .hamburger {
    display: block;

    @media (min-width: 1000px) {
      display: none;
    }
  }

  .desktop-nav {
    display: none;
    justify-content: flex-end;
    justify-items: flex-end;
    align-items: center;
    width: 80%;
    // margin-left: auto;

    .desktop-links {
      display: flex;
      gap: 80px;

      .nav-links-selected {
        color: $red;
      }
    }

    @media (min-width: 1000px) {
      display: flex;
      width: 80%;
    }
  }

  .mobile-nav {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    max-height: calc(100vh - 114px);
    margin-top: 114px;
    background-color: $ash;
    z-index: 20;
    padding: 40px;
  }
}

.layout-footer {
  width: 100%;
  background: #333d47;
  place-self: end;
  .layout-footer-inner {
    width: 95%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 15px 0;
  }

  .footer-logo {
    height: 70px;
    margin-left: auto;
    display: block;
    margin-bottom: 10px;
  }

  .footer-links-container {
    // display: flex;
    // justify-content: space-between;
    width: 90%;
    margin: 0 auto;
    text-align: center;
    // margin-left: auto;

    @media (min-width: 960px) {
      width: 50%;
    }
    .ft-col-1 {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .footer-link {
      display: block;
      margin-bottom: 10px;
      color: white;
      text-decoration: none;
      font-size: 20px;
      margin-right: 50px;
      transition: color 0.5s ease-in-out;

      &:hover {
        color: $red;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    .ft-logo {
      color: white;
      font-size: 16px;
      margin-top: 10px;

      &:first-child {
        margin-top: 0;
      }
    }
  }
}

.draw-button {
  padding: 14px 21px;
  background: $red;
  border-radius: 4px;
  color: white;
  text-decoration: none;
  font-size: 16px;
  transition: opacity 0.5s ease-in-out;
  &:hover {
    opacity: 0.8;
  }
}

.nav-links {
  text-decoration: none;
  color: #333d47;
  font-size: 18px;
  transition: color 0.5s ease-in-out;
  display: block;

  &:hover {
    color: $red;
  }

  @media (max-width: 1000px) {
    color: white;
    margin-bottom: 50px;
  }
}
