@import "variables";

.faq-container {
  padding: 20px 0;
}

.faq-title {
  text-align: center;
  color: $ash;
}

.faq-inner-container {
  margin: 30px 0;
}

.faqs-container {
  width: 90%;
  margin: 40px 0;
}

.faqs-question {
  width: 100%;
  padding: 5px 20px;
  .question-div {
    display: flex;
    align-items: flex-start;
    padding: 10px;

    .question {
      color: $ash;
      font-weight: 500;
      font-size: 24px;
      margin-left: 10px;
      margin-top: -5px;

      .caret {
        margin-right: 20px;
        display: inline-block;
      }
    }
  }

  .answer {
    padding: 10px;
    margin-left: 25px;
    border-top: 0;
  }
}
