@import "./variables";

.videos-container {
  padding: 20px 0;
}

.videos-title {
  text-align: center;
  color: $ash;
}

.videos-inner-container {
  margin: 40px 0;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 2fr));
  // grid-row: ;
  gap: 10px;
  justify-content: space-between;

  .video-title {
    color: $ash;
    font-size: 18px;
    margin: 10px 0;
    word-wrap: break-word;
  }

  .video-sample {
    width: 100%;
    height: 400px;
    aspect-ratio: aspect-ratio;
  }
}
