@import "variables";

.help-container {
  padding: 20px 0;
}

.help-title {
  text-align: center;
  color: $ash;
}

.help-inner-container {
  margin: 30px 0;
}

.toc {
  margin: 20px 0;
  font-size: 25px;
  text-align: left;
}

.content-title {
  display: block;
  text-decoration: none;
  font-size: 20px;
  color: #0000ee;
  margin-bottom: 10px;
}

.content-sub-title {
  display: block;
  text-decoration: none;
  margin-left: 20px;
  font-size: 20px;
  margin-bottom: 10px;
  color: #0000ee;
}

.content-sub-sub-title {
  display: block;
  text-decoration: none;
  margin-left: 60px;
  font-size: 20px;
  margin-bottom: 10px;
  color: #0000ee;
}

.content {
  width: 100%;
  margin: 100px 0;

  h1 {
    font-size: 30px;
    font-weight: 600;
  }

  p {
    margin-top: 10px;
    text-align: justify;
    font-size: 18px;
    line-height: 2;
  }

  h2 {
    font-size: 28px;
    font-weight: 700;
    margin: 20px 0;
  }

  h3 {
    font-size: 20px;
    font-weight: 600;
    margin: 20px 0;
  }

  h4 {
    font-size: 18px;
    font-weight: 600;
    margin: 15px 0;
  }

  img {
    margin: 15px 0;
    border: 3px solid black;
    max-width: 100%;
    height: auto;
  }

  ol,
  li {
    margin-left: 30px;
    margin-bottom: 5px;
  }

  .image-title {
    text-decoration: underline;
    margin: 15px 0;
  }
}

.test-ipad {
  align-self: center;
}
