@import "variables";

.contact-container {
  padding: 20px 0;
}

.contact-title {
  text-align: center;
  color: $ash;
}

.contact-inner-container {
  margin: 30px 0;
  height: 100%;
  display: flex;
  justify-content: center;
}

.contact-text {
  margin-top: 50px;
  font-size: 20px;
  color: $light-ash;
  font-weight: 300;
  text-align: center;
}

.contacts {
  display: flex;
  margin-top: 30px;
  gap: 40px;
  justify-content: center;
  flex-wrap: wrap;

  .contact-link {
    display: flex;
    align-items: flex-end;
    margin-bottom: 10px;
    color: $ash;
    text-decoration: none;
    font-size: 20px;
    margin-right: 20px;
    .contact-icon {
      font-size: 40px;
      color: $ash;
    }

    .contacts-text {
      margin-left: 10px;
      p {
        font-size: 18px;
        text-align: left;
        font-weight: 300;
      }
      a {
        font-weight: 400;
        font-size: 16px;
      }
    }
  }
}
