.landing-hero {
  margin: 40px 0;
  display: flex;
  flex-direction: column;
  justify-items: center;

  & > * {
    flex: 1;
  }

  .hero-text {
    align-self: center;
    text-align: center;
    width: 100%;
    margin-bottom: 40px;
    @media (min-width: 960px) {
      text-align: left;
      margin-bottom: 0;
    }

    h2 {
      line-height: 1.3;
      font-size: 35px;
      color: $ash;
    }

    h4 {
      margin-top: 20px;
      line-height: 1.2;
      font-size: 18px;
      color: $light-ash;
      font-weight: 300;
    }

    .hero-cta {
      padding: 14px 21px;
      background: $red;
      border-radius: 4px;
      color: white;
      text-decoration: none;
      display: inline-block;
      font-size: 16px;
      margin-top: 30px;
      transition: opacity 0.5s ease-in-out;
      &:hover {
        opacity: 0.8;
      }
    }
  }

  .landing-video {
    margin-top: 0;
    width: 90%;
    margin: 0 auto;
    text-align: center;
    display: block;
    height: 450px;
    @media (min-width: 960px) {
      width: 100%;
    }
  }

  @media (min-width: 960px) {
    flex-direction: row;
  }
}

.store-badges {
  display: flex;
  width: 80%;
  margin: 40px auto;
  justify-content: center;
  gap: 10px;

  @media (min-width: 1000px) {
    justify-content: flex-start;
    margin: 30px 0;
  }

  // & > * {
  //   flex: 1;
  // }

  .store-badge {
    height: 50px;
  }
}

.device-containr {
  // width: 650px;
  width: 100%;
  height: 700px;
}

.diagram-hero {
  margin-top: 0;

  .hero-text {
    @media (min-width: 1000px) {
      margin-top: -100px;
    }
  }
}

.img-fluid {
  width: 100%;
  height: 100%;
}

.test-ipad {
  align-self: center;
  justify-self: center;
}
